import React, {Component} from 'react';
import axios from 'axios';
import RecommendationDisplay from './recommendationdisplay';

var url = "https://edumatorestapi.herokuapp.com/restaurantlist"

class RecommendationBlock extends Component{
    constructor(){
        super()

        this.state={
            recommendations:''
        }
    }

    render(){
        return(
            <div>
                <RecommendationDisplay sudata={this.state.recommendations}/>
            </div>
        )
    }

    componentDidMount(){
        let date = new Date().getDate();
        let mealid = sessionStorage.getItem('mealid')
        let outurl;
        if(date>0 && date<11){
            outurl = `${url}/${mealid}?hcost=500&lcost=1000`
        }else if (date>10 && date<21){
            outurl = `${url}/${mealid}?hcost=300&lcost=800`
        }else if(date>20 && date<32){
            outurl = `${url}/${mealid}?hcost=100&lcost=500`
        }
        axios.get(outurl)
        .then((response) => {this.setState({recommendations:response.data})})
    }
}

export default RecommendationBlock;