import React from 'react';
import {Link} from 'react-router-dom';
import './Listing.css';
import Pagination from "react-js-pagination";

const renderList = (props, data) => {
    if (data) {
        var limit = props.limit;
        var page = props.activePage;
        data = data.slice((page - 1) * limit, (page - 1) * limit + limit);
        return data.map((item) => {
            return (
                <div className="item" key={item._id}>
                    <div className="row">
                        <div className="col-md-6 col-sm-11 col-xs-11">
                            <img className="image" src={item.thumb} />
                        </div>
                        <div className="col-md-6 col-sm-11 col-xs-11">
                            <div className="hotel_name heads"><Link to={`/details/${item._id}`} className="heads">{item.name}</Link></div>
                            <div className="hotel_name bottoms">{item.city_name}</div>
                            <div className="hotel_name bottoms">{item.locality}</div>
                            <div className="hotel_name bottoms">{item.address}</div>
                            <div>
                                <span><i className="fa fa-cutlery" aria-hidden="true"></i><i className="fa fa-glass" aria-hidden="true"></i></span>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-sm-3 col-xs-6 ">
                            <div className="cuisine_block">Cuisine</div>
                            <div className="cuisine_block">Cost Per Two.</div>
                        </div>
                        <div className="col-sm-9 col-xs-6">
                            <div className="cuisine_out">{item.Cuisine[0].name} | {item.Cuisine[1].name}</div>
                            <div className="cuisine_out">Rs. {item.cost}</div>
                        </div>
                    </div>
                    <hr />
                </div>

            )
        })
    } else {
        return (
            <center>
                <h5>There is no perfect diet which suits eveyone</h5>
                <img src="/images/foodloader.gif" />
            </center>
        )
    }
}

const handlePageChange = (props, pageNumber) => {
    props.pageNumber(pageNumber);
    var data = props.listData;
    renderList(props, data);
}

const ListingDisplay = (props) => {
    console.log(props)
    return (
        <div>
            <div className="row">
                <div className="col-md-12">
                    {renderList(props, props.listdata)}
                </div>
                <center>
                    <Pagination
                        activePage={props.activePage}
                        itemsCountPerPage={props.limit}
                        totalItemsCount={props.totalNoOfItems}
                        pageRangeDisplayed={5}
                        onChange={(pageNumber) => { handlePageChange(props, pageNumber) }}
	       itemClass="page-item"
                         linkClass="page-link"
                    />



                </center>
            </div>
        </div>
    )
}

export default ListingDisplay;

